<template>
    <div class="vip-opening" :class="showTimeFlag?'bg-normal':'bg-latter'">
        <div class="part-crazy" v-if="showTimeFlag">
            <div class="title">5&nbsp;&nbsp;&nbsp;&nbsp;</div>
            <div class="content">
                <CarzyAppCard v-for="item in applist_crazy" :key="item.title" :info="item" @order="orderHandler(item,false,false,true)"></CarzyAppCard>
            </div>
        </div>
        <div class="tips" :class="!showTimeFlag?'tips-outdate':''">
            <img @click="tipsHandler" :class="userInfo.isVip?'tips-getrights':'tips-openvip'" :src="tipsSrc" alt="">
        </div>
        <div class="part-new" :class="!showTimeFlag?'margin-top56':''">
            <div class="title">
                <span></span><span class="tips-vip"></span>
            </div>
            <div class="content">
                <div class="reward" @click="gotoReward(item.link,index)" v-for="(item,index) in newRewards" :key="item.title">
                    <div class="reward-title">
                        <img :src="item.title" alt="">
                    </div>
                </div>
            </div>
        </div>
        <div class="part-nine">
            <div class="title">
                <span>9</span><span class="tips-vip">1</span>
            </div>
            <div class="content">
                <BarAppCard class="appcard" v-for="item in applist_nine" :key="item.title" :info="item" @order="orderHandler(item)"></BarAppCard>
            </div>
        </div>
        <div class="part-discount">
            <div class="title">
                <span>5</span><span class="tips-vip">1</span>
            </div>
            <div class="content">
                <BarAppCard class="appcard" v-for="item in applist_halfprice" :key="item.title" :info="item" @order="orderHandler(item)"></BarAppCard>
            </div>
        </div>
        <div class="btn-more">
            <img @click="gotolink({name:'home'})" src="@imgs/custompage/2/btn_more.png" alt="">
        </div>
        <SecondConfirmBuy :info="SecondConfirmInfo"></SecondConfirmBuy>
        <div class="toast" :class="'type'+toast.type" v-if="toast.show">
            <div class="content" :style="toast.contentStyle">
                <div v-if="toast.type == 4" class="btn-close-gray" @click="toast.show=false"></div>
                <div>
                    <img v-if="toast.icon" :src="toast.icon" alt="">
                </div>
                <div v-html="toast.tips" class="toast-tips" :style="toast.tipsStyle">
                </div>
                <div v-if="toast.type == 1" class="btn-gotolink" @click="gotolink(toast.link)">
                    {{toast.linkTxt}}
                </div>
                <div v-if="toast.type == 2" class="btns-confirm">
                    <div class="cancel" @click="toast.show=false">{{toast.cancel||''}}</div>
                    <div class="confirm" @click="toast.handlerConfirm()">{{toast.confirm ||''}}</div>
                </div>
                <div v-if="toast.type == 3" class="btns-confirm" style="margin-top:.3rem;">
                    <span class="btn-primary" @click="toast.show=false">{{toast.closeTxt||''}}</span>
                </div>
            </div>
            <div v-if="toast.type == 0" class="btn-close" @click="toast.show=false"></div>
        </div>
        <div class="rules" @click="showRules">
            
        </div>
    </div>
</template>
<script>
    import CarzyAppCard from '@/components/common/AppCard4'
    import BarAppCard from '@/components/common/AppCard5'
    import SecondConfirmBuy from '@/components/common/SecondConfirmBuy'
    import {APPLIST_VIPOPENING,APPLIST_VIPOPENING_NINE,AID_VIPOPENING,VIPORDER,STATISTICS_ACTIVITY} from '@/utils/constant'
    import { mapState,mapMutations } from 'vuex'
    import { getData } from "@/api/halfprice";
    import { Toast } from 'vant';
    import {myVip} from '@/api/vipbenefit';
    import {customAnalysis,pvAnalysis} from "@/assets/js/analysis";
    import messageBus from "@/utils/messageBus";

    export default {
        name: "vip-opening",
        data(){
            return{
                applist_crazy:[],
                applist_nine:[],
                applist_halfprice:[],
                newRewards:[
                    {
                        title: require('@imgs/custompage/2/txt_1G.png'),
                        link: {name: 'newGuest'}
                    }, {
                        title: require('@imgs/custompage/2/txt_didi.png'),
                        link: 'https://dev.coc.10086.cn/coc/web/coc2020/couponCollection/'
                    }, {
                        title: require('@imgs/custompage/2/txt_qinju.png'),
                        link: 'https://dev.coc.10086.cn/coc/web/coc2020/couponCollection/'
                    }, {
                        title: require('@imgs/custompage/2/txt_58.png'),
                        link: 'https://dev.coc.10086.cn/coc/web/coc2020/couponCollection/'
                    }, {
                        title: require('@imgs/custompage/2/txt_jinjiang.png'),
                        link: 'https://dev.coc.10086.cn/coc/web/coc2020/couponCollection/'
                    }, {
                        title: require('@imgs/custompage/2/txt_yuetu.png'),
                        link: 'https://dev.coc.10086.cn/coc/web/coc2020/couponCollection/'
                    }
                ],
                SecondConfirmInfo: {
                    payShow: false,
                    paydetailList: {
                        price: '0',
                    },
                    orderObject: {
                        phone: '',
                        phoneMask: ''
                    },
                    callback:''
                },
                toast: {
                    tips: '',
                    show: false,
                    type: 0,
                    link: '',
                    linkTxt: '',
                    handlerConfirm: '',
                    contentStyle:null,
                    tipsStyle:null
                },
                showTimeFlag: true//
            }
        },
        created(){
            document.title = '618';
            this.applist_crazy = APPLIST_VIPOPENING;
            if(new Date().getTime()>new Date('2020/6/23').getTime()){
                this.showTimeFlag = false;
            }
            this.applist_nine = APPLIST_VIPOPENING_NINE;
            getData().then(res=>{
                let list = res.data.data;
                list.forEach(item => {
                    item.icon = this.Common.getImgUrl(item.icon)
                })
                this.applist_halfprice = list;
            })
            pvAnalysis(STATISTICS_ACTIVITY["618"].activityId);
        },
        methods:{
            ...mapMutations([
                'SET_USERINFO',
            ]),
            orderHandler(order,formatted,isVipOrder,dontneedVip){
                //95
                if(!isVipOrder){
                    customAnalysis(STATISTICS_ACTIVITY["618"].activityId,STATISTICS_ACTIVITY["618"].key_btns,order.saleid);
                }
                if(!this.checkLogin()) return;
                let that = this;
                if(!this.userInfo.isVip && !isVipOrder && !dontneedVip){
                    that.showToast({
                        tips:'<div style="text-align: center"><div></div><div></div></div>',
                        type:2,
                        icon: require("@imgs/custompage/2/tips_info.png"),
                        cancel:'',
                        confirm: '',
                        handlerConfirm: ()=>{
                            that.toast.show = false;
                            let order;
                            order = Object.assign({},VIPORDER);
                            order.name = '';
                            order.channelCode = that.sysInfo.channelCode;
                            order.isVipOrder = true;
                            that.orderHandler(order,true,true);
                        }
                    })
                    return;
                }
                if(order.date){
                    if(new Date().getTime()<new Date(order.startdate).getTime()){
                        Toast.fail('');
                        return;
                    }
                    if(new Date().getTime()>new Date(order.expiredate).getTime()){
                        Toast.fail('');
                        return;
                    }
                }
                let data;
                if (formatted){
                    data = order;
                } else {
                    data= {
                        proId: order.proid,
                        name: order.name,
                        salesId: order.saleid,
                        channelCode: this.sysInfo.channelCode ,
                        dealType: 0,
                        isPay: 1,
                        payType: 10,
                        amount: order.price,
                        orderWay: '1',//
                        aid: AID_VIPOPENING
                    }
                }
                this.SecondConfirmInfo.orderObject.phone = this.userInfo.phone
                this.SecondConfirmInfo.orderObject.phoneMask = this.userInfo.phoneMask
                this.SecondConfirmInfo.paydetailList = data;
                this.SecondConfirmInfo.payShow = true;
                this.SecondConfirmInfo.callback = res =>{
                    that.SecondConfirmInfo.payShow = false;
                    if (res.resultCode == 0) {
                        customAnalysis(STATISTICS_ACTIVITY["618"].activityId,STATISTICS_ACTIVITY["618"].key_sales,order.saleid);
                        that.showToast({
                            tips:`<div style="text-align: center"><div style="color:#fc7074;font-size:1.2em">!</div><div></div><div></div><div style="font-size:.65em;">""---""</div></div>`,
                            closeTxt: '',
                            type: 3,
                            icon: require("@imgs/custompage/2/tips_success.png"),
                        })
                        if (isVipOrder) {
                            that.getVipInfo();
                        }
                    } else if(res.resultCode == -137){
                        that.showToast({
                            tips:`<div style="text-align: center"><div></div><div></div><div></div></div>`,
                            type: 1,
                            linkTxt:'',
                            link: {name:'vipBenefit'},
                            icon: require("@imgs/custompage/2/tips_fail.png"),
                        })
                    }else{
                        Toast.fail({message: res.msg, duration: 4000});
                    }
                }
            },
            tipsHandler(){
                if(!this.userInfo.isVip) {
                    let order;
                    order = Object.assign({},VIPORDER);
                    order.name = '';
                    order.channelCode = this.sysInfo.channelCode;
                    order.isVipOrder = true;
                    //
                    customAnalysis(STATISTICS_ACTIVITY["618"].activityId,STATISTICS_ACTIVITY["618"].key_btns,order.salesId);
                    this.orderHandler(order,true,true);
                }
            },
            checkLogin(){
                if(!this.userInfo.phone){
                    Toast.fail('');
                    let backUrl = encodeURIComponent(this.$route.fullPath);
                    if(this.sysInfo.channel == 'st'){
                        messageBus.$emit('msg_checkLogin','init');
                    }else{
                        this.$router.replace({name: 'login', query: {'backUrl': backUrl}});
                    }
                    return false;
                }else {
                    return true;
                }
            },
            showRules(){
                let rules = `<p><b><font></font></b></p><p>11301365</p><p>2-</p><p>3</p><p>4---</p><p>5</p><p>6243</p><p>7</p><p>&nbsp;</p><p><b><font></font></b></p><p>1<font></font><font></font>5/<font></font><font></font><font></font><font></font><font></font>, <font></font><font></font></p><p>2<font></font><font></font></p><p>3.</p><p><span style="color: rgb(0, 0, 0);">&nbsp;</span></p><p><b><font></font>618</b></p><p><b><span style="color: rgb(0, 0, 0);">1-</span></b><b></b></p><p><span style="color: rgb(0, 0, 0);"><font></font>1</span></p><p><span style="color: rgb(0, 0, 0);"><font></font>2</span></p><p><span style="color: rgb(0, 0, 0);">a</span><span style="color: rgb(0, 0, 0);">.-</span><span style="color: rgb(0, 0, 0);">6</span><span style="color: rgb(0, 0, 0);"><font></font>18-620</span></p><p><span style="color: rgb(0, 0, 0);">b.</span><span style="color: rgb(0, 0, 0);"><font></font>T</span><span style="color: rgb(0, 0, 0);">V</span><span style="color: rgb(0, 0, 0);"><font></font>-</span><span style="color: rgb(0, 0, 0);">6</span><span style="color: rgb(0, 0, 0);"><font></font>18-621</span></p><p><span style="color: rgb(0, 0, 0);">c</span><span style="color: rgb(0, 0, 0);">.</span><span style="color: rgb(0, 0, 0);"><font></font>-</span><span style="color: rgb(0, 0, 0);">6</span><span style="color: rgb(0, 0, 0);"><font></font>18-622</span></p><p><span style="color: rgb(0, 0, 0);"><font></font>3</span></p><p><span style="color: rgb(0, 0, 0);"><font></font>4</span></p><p><span style="color: rgb(0, 0, 0);">&nbsp;</span></p><p><b><span style="color: rgb(0, 0, 0);">2</span></b><b></b></p><p><span style="color: rgb(0, 0, 0);"><font></font>1</span></p><p><span style="color: rgb(0, 0, 0);"><font></font>11</span><span style="color: rgb(0, 0, 0);">GB</span><span style="color: rgb(0, 0, 0);"><font></font></span><font></font>2424</p><p><font></font>25</p><p><font></font>3</p><p><font></font>4</p><p><b><span style="color: rgb(0, 0, 0);">35</span></b><b></b></p><p><span style="color: rgb(0, 0, 0);"><font></font>1</span></p><p><span style="color: rgb(0, 0, 0);"><font></font>211</span></p><p><span style="color: rgb(0, 0, 0);"><font></font>35618</span></p><p><b><span style="color: rgb(0, 0, 0);">49</span></b><b></b></p><p><span style="color: rgb(0, 0, 0);"><font></font>1</span></p><p><span style="color: rgb(0, 0, 0);"><font></font>211</span></p><p><span style="color: rgb(0, 0, 0);"><font></font>39618</span></p><p><br></p>`;
                this.showToast({
                    type: 4,
                    tips: rules,
                    contentStyle: {
                        width: '70%',
                        margin: '0'
                    },
                    tipsStyle: {
                        height: '60vh',
                        overflow: 'scroll'
                    }
                });
            },
            getVipInfo(){
                let that = this;
                myVip({
                    proId:'6000692',
                    salesId:'102125',
                    channelCode:that.sysInfo.channelCode,
                    phone:that.userInfo.phone,
                }).then((res)=>{
                    if(res.data.resultCode == 0){ //vip
                        res.data.data.isVip = 1;
                        that.SET_USERINFO(res.data.data);
                    }else{
                        that.SET_USERINFO({isVip:0});
                    }
                })
            },
            gotoReward(link,index){
                let that = this;
                if(!this.checkLogin()) return;
                if(!this.userInfo.isVip) {
                    that.showToast({
                        tips:'<div style="text-align: center"><div></div><div></div></div>',
                        type:2,
                        icon: require("@imgs/custompage/2/tips_info.png"),
                        cancel:'',
                        confirm: '',
                        handlerConfirm: ()=>{
                            that.toast.show = false;
                            let order;
                            order = Object.assign({},VIPORDER);
                            order.name = '';
                            order.channelCode = that.sysInfo.channelCode;
                            order.isVipOrder = true;
                            that.orderHandler(order,true,true);
                        }
                    })
                    return;
                }
                customAnalysis(STATISTICS_ACTIVITY["618"].activityId,STATISTICS_ACTIVITY["618"].key_new,index);
                if(typeof link == 'string') {
                    location.href = link + '?pnsign=' + this.userInfo.pnsign
                }else {
                    this.$router.push(link)
                }
            },
            gotolink(link){
                if(typeof link == 'string') {
                    location.href = link;
                }else {
                    this.$router.push(link)
                }
            },
            showToast(toast) {
                this.toast = Object.assign({
                    tips: '',
                    show: false,
                    type: 0,
                    link: '',
                    linkTxt: '',
                    handlerConfirm: '',
                    contentStyle:null,
                    tipsStyle:null
                },toast);
                this.toast.show = true;
            },
        },
        computed:{
            ...mapState([ 
                "userInfo" ,
                "sysInfo"
            ]),
            tipsSrc(){
                return this.userInfo.isVip?require('../../assets/imgs/custompage/2/tips_getRights.png'):require('../../assets/imgs/custompage/2/tips_openvip.png');
            }
        },
        components: {
            CarzyAppCard,
            BarAppCard,
            SecondConfirmBuy
        }
    }
</script>
