import request from '@/utils/request'

export function getCategory() { // 
    return request({
        url: '/api/home/category',
        method: 'get',
    })
}

export function getFindMembers(data) { // 
    return request({
        url: '/api/qymb/findMembers',
        method: 'post',
        data: data,
    })
}