export default function flexible() {
    var win = window;
    var remCalc = {};
    var docEl = win.document.documentElement,
        tid;

    function refreshRem() {
        // 
        var width = docEl.getBoundingClientRect().width;
        // 640px 640
        // if (width > 640) { width = 640 }
        // 10 10rem
        // 640  640/10=64px  1rem = 64px
        // 640 80px*80px rem  80/64 = 1.25rem
        //   1.25rem * 1.25rem
        //  320px
        //  1rem = 32px
        //  80px*80px 1.25rem * 32px = 40px
        //  40px * 40px
        // 
        //
        // cms
        //  6.46.4rem
        //  1rem = 100px
        // 640 80px*80px rem  80/100 = 0.8rem
        // ....
        //
        //
        // 
        // rem   64  100
        // 100  64 
        // sass @function px2rem
        // .8rem  px2rem(80)
        //
        //
        var rem = width / 750 * 100;
        docEl.style.fontSize = rem + "px";
        remCalc.rem = rem;
        //
        var actualSize = parseFloat(window.getComputedStyle(document.documentElement)["font-size"]);
        if (actualSize !== rem && actualSize > 0 && Math.abs(actualSize - rem) > 1) {
            var remScaled = rem * rem / actualSize;
            docEl.style.fontSize = remScaled + "px"
        }
    }

    //
    function dbcRefresh() {
        clearTimeout(tid);
        tid = setTimeout(refreshRem, 100)
    }

    //font-size
    win.addEventListener("resize", function() { dbcRefresh() }, false);

    //
    win.addEventListener("pageshow", function(e) {
        if (e.persisted) { dbcRefresh() }//bfcache
    }, false);
    refreshRem();
    remCalc.refreshRem = refreshRem;
    remCalc.rem2px = function(d) {
        var val = parseFloat(d) * this.rem;
        if (typeof d === "string" && d.match(/rem$/)) { val += "px" }
        return val
    };
    remCalc.px2rem = function(d) {
        var val = parseFloat(d) / this.rem;
        if (typeof d === "string" && d.match(/px$/)) { val += "rem" }
        return val
    };
    win.remCalc = remCalc
}
