import Vue from 'vue'
import Vuex from 'vuex'
import App from './App.vue'
import router from './router'
import store from './store'
import Vant from 'vant';
import { Lazyload } from 'vant';
import 'vant/lib/index.css';
import Common from './common';
import VueAwesomeSwiper from 'vue-awesome-swiper';
import 'swiper/css/swiper.css';
import {is_weixin} from '@/utils/func';


Vue.use(Vant).use(Lazyload);
Vue.use(Common);
Vue.use(VueAwesomeSwiper );


Vue.use(Vuex);
Vue.config.productionTip = false
window.preRoute = ''

router.beforeEach((to, from, next) => {
    window.preRoute = Object.assign({},from);

    /* title */
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    if (is_weixin()){
        let shareData;
        if (to.name == 'custompage' && to.params.customId==2){
            shareData = {
                title: "618",
                desc: "app59",
                link: location.origin + '/' + location.search + '#' + to.fullPath,
                imgUrl: location.origin + '/imgs/pro/share_618.png',
            }
        }else{
            shareData = {
                title: "",
                desc: "",
                link: location.origin + '/' + location.search + '#' + to.fullPath,
                imgUrl: location.origin + '/imgs/pro/share.png',
            }
        }
        share(shareData)
    }
    next();

    function share(data) {
        window.wx.ready(function () {
            window.wx.onMenuShareAppMessage({
                title: data.title, // 
                desc: data.desc, // 
                link: data.link, // JS
                imgUrl: data.imgUrl ? data.imgUrl : location.origin + '/imgs/pro/share.png', // 
                type: '', // ,musicvideolinklink
                dataUrl: '', // typemusicvideo
                success: function () {
                    // 
                },
                cancel: function () {
                    // 
                }
            });

            window.wx.onMenuShareTimeline({
                title: data.name, // 
                link: data.link, // JS
                imgUrl: data.imgUrl ? data.imgUrl : location.origin + '/imgs/pro/share.png', // 
                success: function () {
                    // 
                    console.log()
                },
                cancel: function () {
                    // 
                }
            });

            // configreadyconfigconfigreadyready
        });
    }
})

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
