<template>
    <div class="classification-item2" @click.stop="jump">
        <div class="inner">
            <div class="img-wrapper">
            <img :src="item.img" alt="" :style="{height:item.h?item.h:'auto'}">
            </div>
            <div class="title">{{item.title}}</div>
        </div>
    </div>
</template>

<script>
    import messageBus from "@/utils/messageBus";
    import { mapState } from 'vuex';

    export default {
        name: "classification-item2",
        props:['item'],
        computed:{
            ...mapState([
                "userInfo",
                "sysInfo"
            ])
        },
        methods:{
            jump(){
                let that = this;
                if(that.item.rights == 'getRights'){
                    // 
                    if(that.userInfo.phone){
                        that.$router.push({name:'getRights'})
                    }else{
                        messageBus.$emit('msg_checkLogin','init')
                    }
                }else{
                    if(that.sysInfo.channel == 'st'){
                        window.leadeon.newWebview({
                            debug: false,
                            markID: '',                     // ID id4.04.0
                            type: '',           //  4.0 
                            funCode: '',        //  4.0  V4.0_
                            bizCode: '', //()
                            url: that.item.linkurl, // url1URL2encodeURI(),decodeURI()http://www.w3school.com.cn/jsref/jsref_encodeuri.asp
                            success: function(res) {console.log(res)},
                            error: function(err) {console.log(err)}
                        });
                    }else{
                        window.location.href = that.item.linkurl
                    }
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .classification-item2{
        width:1.7rem;
        background: rgba(0.0.0,2);
        float: left;
        display: flex;
        justify-content: center;
        .img-wrapper{
            text-align: center;
            display: block;
            height:0.78rem;
            img{
                max-width:100%;
                max-height:100%;
            }
        }
        .title{
            font-size: 0.27rem;
            padding-top: 0.09rem;
            white-space: nowrap;  /**/
        }
    }
</style>